import { DotLottieReact } from '@lottiefiles/dotlottie-react';

import type { DotLottieReactProps } from '@lottiefiles/dotlottie-react';

export const icons = [
	'empty-file',
	'image-loading-improved',
	'lottie-logo',
	'love-message-burst',
	'weather-broken-clouds',
	'weather-clear-sky',
	'weather-few-clouds',
	'weather-mist',
	'weather-night-broken-clouds',
	'weather-night-clear-sky',
	'weather-night-few-clouds',
	'weather-night-mist',
	'weather-night-rain',
	'weather-night-scattered-clouds',
	'weather-night-shower-rains',
	'weather-night-thunderstorm',
	'weather-rain',
	'weather-scattered-clouds',
	'weather-shower-rains',
	'weather-snow',
	'weather-thunderstorm',
	'web-development'
] as const;

export type DotLottieIconName = typeof icons[number];

export interface DotLottieIconProps extends Omit<DotLottieReactProps, 'src'>
{
	/**
	 * 아이콘 이름
	 */
	readonly iconName: DotLottieIconName;
}

/**
 * dotLottie 아이콘 atom 컴포넌트 반환 메서드
 *
 * @param {DotLottieIconProps} param0: DotLottieIconProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function DotLottieIcon({ iconName, ...props }: DotLottieIconProps): React.JSX.Element
{
	return (
		<DotLottieReact
			data-component="DotLottieIcon"
			src={`https://project.itcode.dev/lottie/lottie/${iconName}.lottie`}
			{...props}
		/>
	);
}